body {
margin: 0;
}
.watch {
  position: relative;
  width: 1000px;
  height: 500px;
  border: 1px solid blue;
  margin-left: 0;
  
}
.seconds {
  position: absolute;
  left: 50%;
  top: 50%;
 transform: translateX(-50%);
  background-color: greenyellow;
  border-radius: 30%;
  width: 10px;
  height: 200px;
  animation: minute 60s linear infinite;
  transform-origin: 5px 200px;
  animation-play-state: paused;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: navy;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

@keyframes minute {
  from {
    transform: rotate();
  }
  to {
    transform: rotate(360deg);
  
  }
}
@keyframes hour {
  from {
    transform: rotate();
  }
  to {
    transform: rotate(360deg);
  
  }
}

.minutes {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  bottom: 50%;
  background-color: rgb(55, 0, 255);
  border: 1px solid red;
  border-radius: 30%;
  width: 5px;
  height: 150px;
  animation: hour 120s linear infinite;
  // transform-origin: 2.5px 300px;

}